import React, { useState, useEffect } from 'react';
import firebaseService from '../services/firebaseService';
import '../styles/AuthWrapper.css';

function AuthWrapper({ children }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showAuth, setShowAuth] = useState(false);
  const [authMode, setAuthMode] = useState('signin'); // 'signin' or 'signup'
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  useEffect(() => {
    const unsubscribe = firebaseService.onAuthStateChanged((user) => {
      setUser(user);
      setLoading(false);
    });

    return () => unsubscribe();
  }, []);

  const handleAnonymousSignIn = async () => {
    try {
      setLoading(true);
      setError('');
      await firebaseService.signInAnonymously();
    } catch (error) {
      setError('  : ' + error.message);
      setLoading(false);
    }
  };

  const handleEmailAuth = async (e) => {
    e.preventDefault();
    try {
      setLoading(true);
      setError('');
      
      if (authMode === 'signin') {
        await firebaseService.signInWithEmail(email, password);
      } else {
        await firebaseService.createUserWithEmail(email, password);
      }
    } catch (error) {
      setError(authMode === 'signin' ? ' : ' + error.message : ' : ' + error.message);
      setLoading(false);
    }
  };

  const handleSignOut = async () => {
    try {
      await firebaseService.signOut();
    } catch (error) {
      console.error('Sign out error:', error);
    }
  };

  if (loading) {
    return (
      <div className="auth-loading">
        <div className="loading-spinner"></div>
        <p> ...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="auth-container">
        <div className="auth-wrapper">
          <div className="auth-header">
            <h1>Vision -  </h1>
            <p>     </p>
          </div>

          {!showAuth ? (
            <div className="auth-options">
              <button 
                className="auth-btn anonymous-btn" 
                onClick={handleAnonymousSignIn}
                disabled={loading}
              >
                 
              </button>
              <button 
                className="auth-btn email-btn" 
                onClick={() => setShowAuth(true)}
              >
                 
              </button>
            </div>
          ) : (
            <div className="auth-form-container">
              <div className="auth-toggle">
                <button 
                  className={`toggle-btn ${authMode === 'signin' ? 'active' : ''}`}
                  onClick={() => setAuthMode('signin')}
                >
                  
                </button>
                <button 
                  className={`toggle-btn ${authMode === 'signup' ? 'active' : ''}`}
                  onClick={() => setAuthMode('signup')}
                >
                  
                </button>
              </div>

              <form onSubmit={handleEmailAuth} className="auth-form">
                <div className="form-group">
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder=""
                    required
                    className="auth-input"
                  />
                </div>
                <div className="form-group">
                  <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder=""
                    required
                    className="auth-input"
                  />
                </div>
                
                {error && <div className="auth-error">{error}</div>}
                
                <button 
                  type="submit" 
                  className="auth-btn submit-btn"
                  disabled={loading}
                >
                  {authMode === 'signin' ? '' : ''}
                </button>
              </form>

              <button 
                className="back-btn" 
                onClick={() => setShowAuth(false)}
              >
                
              </button>
            </div>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="authenticated-container">
      <div className="user-info">
        <span>, {user.email || ' '}!</span>
        <button onClick={handleSignOut} className="signout-btn">
          
        </button>
      </div>
      {children}
    </div>
  );
}

export default AuthWrapper;